import React, { useRef, useState } from "react";
import { useEffect } from "react";
import { Link } from "react-scroll";
import { useParallax } from "react-scroll-parallax";
import { Collapse } from "flowbite";
import { FormattedMessage } from "react-intl";

export default function Header(props) {
  const headerRef = useParallax({
    translateY: [20, -10],
  });
  const targetElRef = useRef(null);
  const triggerElRef = useRef(null);
  const [collapse, setCollapse] = useState();
  useEffect(() => {
    const instanceOptions = {
      id: "navbar-default",
      override: true,
    };

    const handleTrackCollapse = (e) => {
      if (targetElRef.current) {
        if (!headerRef.ref.current.contains(e.target)) {
          document.removeEventListener("click", handleTrackCollapse);
          collapse.collapse();
        }
      }
    };

    const options = {
      onCollapse: () => {
        console.log("element has been collapsed");
      },
      onExpand: () => {
        document.addEventListener("click", handleTrackCollapse);
      },
      onToggle: () => {
        console.log("element has been toggled");
      },
    };
    const collapse = new Collapse(
      targetElRef.current,
      triggerElRef.current,
      options,
      instanceOptions,
    );
    setCollapse(collapse);
  }, []);

  return (
    <header
      className="abh sticky left-0 top-0 z-50 w-full rounded-sm bg-dark-500"
      ref={headerRef.ref}
    >
      <nav className="flex-dark-500 p-7 px-3 text-xl text-primary-50">
        <div className="w-fulls flex flex-row flex-wrap items-center justify-between">
          <div className="flex flex-shrink-0 items-center gap-4">
            <img src={props.avt} className="w-10" alt="Flowbite Logo" />
            <span>Ca Vnh Thun</span>
          </div>
          <button
            type="button"
            className="inline-flex h-10 w-10 items-center justify-center rounded-lg p-2 text-sm text-gray-500 hover:bg-gray-100 focus:outline-none focus:ring-2 focus:ring-gray-200 dark:text-gray-400 dark:hover:bg-gray-700 dark:focus:ring-gray-600 md:hidden"
            ref={triggerElRef}
          >
            <span class="sr-only">Open main menu</span>
            <svg
              class="h-5 w-5"
              aria-hidden="true"
              xmlns="http://www.w3.org/2000/svg"
              fill="none"
              viewBox="0 0 17 14"
            >
              <path
                stroke="currentColor"
                stroke-linecap="round"
                stroke-linejoin="round"
                stroke-width="2"
                d="M1 1h15M1 7h15M1 13h15"
              />
            </svg>
          </button>
          <div
            className="hidden w-full items-end duration-700 md:block md:w-auto"
            id="navbar-default"
            ref={targetElRef}
          >
            <ul className="ml-auto mt-4 flex flex-col justify-end gap-2 text-2xl md:mt-0 md:flex-row md:gap-6">
              <li>
                <Link
                  spy={true}
                  smooth={true}
                  duration={500}
                  to="About"
                  className="hover:text-secondary-500"
                  activeClass="text-primary-200 font-bold hover:text-primary-500"
                  onClick={() => collapse.collapse()}
                >
                  <FormattedMessage id="header_about" />
                </Link>
              </li>
              <li>
                <Link
                  spy={true}
                  smooth={true}
                  duration={500}
                  offset={-120}
                  to="Project"
                  className="hover:text-secondary-500"
                  activeClass="text-primary-200 font-bold hover:text-primary-500"
                  onClick={() => collapse.collapse()}
                >
                  <FormattedMessage id="header_project" />
                </Link>
              </li>
              <li>
                <Link
                  spy={true}
                  smooth={true}
                  duration={500}
                  to="Experimence"
                  offset={-120}
                  className="hover:text-secondary-500"
                  activeClass="text-primary-200 font-bold hover:text-primary-500"
                  onClick={() => collapse.collapse()}
                >
                  <FormattedMessage id="header_experience" />
                </Link>
              </li>
            </ul>
          </div>
        </div>
      </nav>
    </header>
  );
}
