import React from "react";
import Each from "./Each";
import PropTypes from "prop-types";

function StackProjects({ data, lang }) {
  function splitArray(array, chunkSize) {
    var result = [];
    for (var i = 0; i < array.length; i += chunkSize) {
      result.push(array.slice(i, i + chunkSize));
    }

    // If the last chunk is smaller than the chunk size, fill it with null values
    var lastChunk = result[result.length - 1];
    if (lastChunk.length < chunkSize) {
      var remainingLength = chunkSize - lastChunk.length;
      for (var j = 0; j < remainingLength; j++) {
        lastChunk.push(null);
      }
    }

    return result;
  }
  return (
    <Each
      of={splitArray(data, 4)}
      render={(projects) => (
        <div className="mt-5 box-border flex h-max flex-row flex-wrap items-start justify-between divide-secondary-300 rounded-sm px-4 shadow-lg odd:bg-primary-500 even:bg-dark-500 even:text-primary-50 lg:divide-x lg:p-8">
          <Each
            of={projects}
            render={(item) =>
              item ? (
                <div className="min-w-80 max-w-xs flex-1 self-stretch lg:px-8">
                  <div className="mx-auto flex h-full flex-col rounded-lg ">
                    <a href={item.preview} className="flex-shrink-0 basis-72">
                      <img
                        className="aspect-square w-full rounded-t-lg object-contain"
                        src={item.background}
                        alt="logo"
                      />
                    </a>
                    <div className="flex flex-shrink-0 flex-grow flex-col p-5">
                      <a href={item.preview}>
                        <h5 className="dark-500:text-white mb-1 text-2xl font-bold tracking-tight">
                          {item.name}
                        </h5>
                      </a>
                      <p className=" dark-500:text-gray-400 mb-3 text-justify font-normal">
                        <nav>({item.date})</nav> {item.des[lang]}
                      </p>
                      <nav className="my-auto">
                        <strong>{item.codelanguage.join(", ")}</strong>
                      </nav>
                      <a
                        href={item.preview}
                        className="dark-500:bg-blue-600 dark-500:hover:bg-blue-700 dark-500:focus:ring-blue-800 inline-flex items-end rounded-lg bg-secondary-700 px-3 py-2 text-center text-sm font-medium text-white hover:bg-blue-800 focus:outline-none focus:ring-4 focus:ring-blue-300"
                      >
                        View source
                        <svg
                          className="ms-2 h-3.5 w-3.5 rtl:rotate-180"
                          aria-hidden="true"
                          xmlns="http://www.w3.org/2000/svg"
                          fill="none"
                          viewBox="0 0 14 10"
                        >
                          <path
                            stroke="currentColor"
                            stroke-linecap="round"
                            stroke-linejoin="round"
                            stroke-width="2"
                            d="M1 5h12m0 0L9 1m4 4L9 9"
                          />
                        </svg>
                      </a>
                    </div>
                  </div>
                </div>
              ) : (
                <div className="min-w-72 flex-1 lg:px-8">
                  <div className="h-full w-72 rounded-lg bg-secondary-500 text-center opacity-5 even:bg-dark-500"></div>
                </div>
              )
            }
          />
        </div>
      )}
    />
  );
}

StackProjects.propTypes = {
  data: PropTypes.arrayOf(
    PropTypes.shape({
      background: PropTypes.string.isRequired,
      name: PropTypes.string.isRequired,
      date: PropTypes.string.isRequired,
      des: PropTypes.string.isRequired,
      codelanguage: PropTypes.arrayOf(PropTypes.string).isRequired,
    }),
  ).isRequired,
};
export default StackProjects;
