import Each from "./Each";
export default function SectionHeader({
  options,
  holder,
  onFilter,
  data,
  title,
  des,
}) {
  const handleSelectChange = (e) => {
    const val = e.target.value;
    console.log(val);
    if (onFilter && val >= 0) {
      onFilter(filterProjectsByCodelanguage(data, options[val]));
      return;
    }
    onFilter(data);
  };

  function filterProjectsByCodelanguage(projects, language) {
    return projects.filter((project) =>
      project.codelanguage.includes(language),
    );
  }

  return (
    <div className="abh-bold flex items-center justify-between rounded-3xl bg-dark-500 p-3 px-10 drop-shadow-md">
      <div>
        <h2 className="text-2xl text-primary-50 md:text-4xl">{title}</h2>
        <span className="abh text-lg text-primary-50 md:text-xl">{des}</span>
      </div>
      {options ? (
        <div className="flex items-center justify-center">
          <form class=" max-w-md">
            <select
              class="border-gray-30 block w-full rounded-lg border bg-dark-500 p-2.5 text-sm text-primary-500 focus:border-blue-500 focus:ring-blue-500 dark:border-gray-600 dark:bg-gray-700 dark:text-white dark:placeholder-gray-400 dark:focus:border-blue-500 dark:focus:ring-blue-500"
              onChange={handleSelectChange}
            >
              <option selected value={-1}>
                {holder ?? "Select your value"}
              </option>
              <Each
                of={options}
                render={(item, index) => (
                  <option value={index} key={index}>
                    {item}
                  </option>
                )}
              />
            </select>
          </form>
        </div>
      ) : null}
    </div>
  );
}
