import { useEffect } from "react";
import { useState } from "react";
import PropTypes from "prop-types";
import Each from "./Each";
import "flowbite";

function LanguageOptions(props) {
  const [optionsSelected, setOptionSelected] = useState(null);
  useEffect(() => {
    if (!optionsSelected) {
      handleOnChange(props.data[0]);
    }
  }, []);

  const handleOnChange = (lang) => {
    console.log("handleOnChange", lang);
    setOptionSelected(lang);
    if (props.onChange) props.onChange(lang);
  };

  return (
    <div class="fixed -right-0 bottom-1 z-50 mr-2 flex items-center space-x-1 rounded-lg bg-secondary-200 text-dark-500 md:order-2 md:space-x-0 rtl:space-x-reverse">
      <button
        type="button"
        data-dropdown-toggle="language-dropdown-menu"
        class="inline-flex cursor-pointer items-center justify-center rounded-lg px-4 py-2 text-sm font-medium text-dark-500 hover:bg-gray-100 dark:text-white dark:hover:bg-gray-700 dark:hover:text-white md:indent-0"
      >
        <span className={optionsSelected?.flagIconClass}></span>
        <span className="ms-3 hidden text-sm font-medium md:inline-block">
          {optionsSelected?.title}
        </span>
      </button>
      <div
        className="z-50 my-4 mr-4 hidden list-none divide-y divide-gray-100 rounded-lg bg-white text-base shadow dark:bg-gray-700"
        id="language-dropdown-menu"
      >
        <ul class="py-2 font-medium" role="none">
          <Each
            of={props.data}
            render={(lang) => (
              <li>
                <button
                  class="block px-4 py-2 text-sm text-gray-700 hover:bg-gray-100 dark:text-gray-400 dark:hover:bg-gray-600 dark:hover:text-white"
                  role="menuitem"
                  onClick={() => handleOnChange(lang)}
                >
                  <div class="inline-flex items-center">
                    <span className={lang.flagIconClass}></span>
                    <span className="ms-3">{lang.title}</span>
                  </div>
                </button>
              </li>
            )}
          />
        </ul>
      </div>
    </div>
  );
}

LanguageOptions.propTypes = {
  data: PropTypes.arrayOf(
    PropTypes.shape({
      title: PropTypes.string.isRequired,
      flagIconClass: PropTypes.string.isRequired,
      value: PropTypes.string.isRequired,
    }),
  ).isRequired,
  onChange: PropTypes.func.isRequired,
};

export default LanguageOptions;
